<template>
	<view>
		<u-button @click="choose">选取文件</u-button>
		<u-button>提交文件</u-button>
	</view>
</template>

<script>
export default {
	data() {
		return {};
	},
	methods: {
		choose() {
			// 选择图片
			uni.chooseImage({
				// 选择图片的数量
				count: 1,
				// 图片选择成功后执行的方法
				success: res => {
					console.log("成功获取到临时文件路径=>",res.tempFilePaths[0])
					// 1.获取到文件的路径 -> 调用图片转Base64的方法
					this.getBase64(res.tempFilePaths[0]);
					// this.imageList = this.imageList(res.tempFilePaths);
					// console.log(res);
					// // this.tempFilePaths = res.tempFilePaths[0];
					// // var base64 = this.urlTobase64(res.tempFilePaths[0]);
					// const base64 = uni.arrayBufferToBase64(res.tempFilePaths[0]);
					// console.log(base64);
				},
			})
		},
		getBase64:function(a){
			// 获取到全局的文件管理器
			var manager = uni.getFileSystemManager();
			// 通过文件管理器读取文件内容
			 manager.readFile({
				 // 要读取的文件路径（本地路径）
				 filePath:a,
				 // 以Base64位编码读取文件内容
				 encoding:"base64",
				 success:res =>{
					 console.log("图片转换出来的base64 =>","data:image/jpeg;base64,"+res.data)
				 }
			 })
		}
		// // 转base64码
		// urlTobase64(url) {
		// 	uni.request({
		// 		url: url,
		// 		method: 'GET',
		// 		responseType: 'arraybuffer',
		// 		success: res => {
		// 			let base64 = uni.arrayBufferToBase64(res.data); //把arraybuffer转成base64
		// 			base64 = 'data:image/jpeg;base64,' + base64; //不加上这串字符，在页面无法显示
		// 			return base64;
		// 		}
		// 	});
		// }
	}
};
</script>

